/* Glass effect with orange and blue gradient */
.glass-effect {
  background: linear-gradient(135deg, rgba(53, 101, 117, 0.2), rgba(53, 101, 117, 0.1)) !important;
  backdrop-filter: blur(10px) !important;
  -webkit-backdrop-filter: blur(10px) !important;
  border: 1px solid rgba(255, 255, 255, 0.1) !important;
  box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.1) !important;
}

/* Gradient background for the app */
.app-background {
  min-height: 100vh;
  background: linear-gradient(135deg, #356575 75%, #e67e22 100%);
  position: relative;
  overflow: hidden;
}

.app-background::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(circle at 50% 50%, rgba(211, 84, 0, 0.2), rgba(53, 101, 117, 0.2));
  animation: pulse 8s ease-in-out infinite;
}

@keyframes pulse {
  0% {
    opacity: 0.5;
    transform: scale(1);
  }
  50% {
    opacity: 1;
    transform: scale(1.2);
  }
  100% {
    opacity: 0.5;
    transform: scale(1);
  }
}

/* Common button styles */
.orange-button {
  background: linear-gradient(45deg, #d35400, #e67e22) !important;
  color: white !important;
  transition: all 0.3s ease !important;
  border: none !important;
  box-shadow: 0 4px 15px rgba(211, 84, 0, 0.3) !important;
}

.orange-button:hover {
  background: linear-gradient(45deg, #e67e22, #d35400) !important;
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(211, 84, 0, 0.4) !important;
}

/* Table styles */
.themed-table {
  background: rgba(26, 26, 26, 0.7) !important;
  backdrop-filter: blur(10px) !important;
  border-radius: 12px !important;
  overflow: hidden !important;
}

.themed-table th {
  background: linear-gradient(90deg, rgba(211, 84, 0, 0.2), rgba(53, 101, 117, 0.2)) !important;
  color: white !important;
  font-weight: bold !important;
  border-bottom: 2px solid rgba(211, 84, 0, 0.3) !important;
  padding: 16px !important;
}

.themed-table td {
  color: white !important;
  border-bottom: 1px solid rgba(53, 101, 117, 0.2) !important;
  padding: 12px 16px !important;
}

.themed-table tr:hover {
  background: linear-gradient(90deg, rgba(211, 84, 0, 0.1), rgba(53, 101, 117, 0.1)) !important;
}

/* Form field styles */
.themed-input .MuiOutlinedInput-root {
  background: rgba(26, 26, 26, 0.7) !important;
  backdrop-filter: blur(5px) !important;
  border-radius: 8px !important;
  color: white !important;
}

.themed-input .MuiOutlinedInput-root .MuiOutlinedInput-notchedOutline {
  border-color: rgba(211, 84, 0, 0.3) !important;
  transition: border-color 0.3s ease !important;
}

.themed-input:hover .MuiOutlinedInput-root .MuiOutlinedInput-notchedOutline {
  border-color: rgba(211, 84, 0, 0.5) !important;
}

.themed-input .MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-notchedOutline {
  border-color: #d35400 !important;
  border-width: 2px !important;
  box-shadow: 0 0 0 2px rgba(211, 84, 0, 0.2) !important;
}

.themed-input .MuiInputLabel-root {
  color: rgba(255, 255, 255, 0.7) !important;
  font-weight: 500 !important;
}

.themed-input .MuiInputLabel-root.Mui-focused {
  color: #d35400 !important;
}

/* Card styles */
.themed-card {
  background: rgba(26, 26, 26, 0.7) !important;
  backdrop-filter: blur(10px) !important;
  border-radius: 16px !important;
  border: 1px solid rgba(211, 84, 0, 0.1) !important;
  transition: all 0.3s ease !important;
  overflow: hidden !important;
}

.themed-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, #d35400, #356575) !important;
}

.themed-card:hover {
  transform: translateY(-4px) !important;
  box-shadow: 0 12px 40px rgba(211, 84, 0, 0.2) !important;
  border-color: rgba(211, 84, 0, 0.2) !important;
}

/* Navigation styles */
.nav-link {
  color: white !important;
  position: relative !important;
  padding: 8px 16px !important;
  transition: all 0.3s ease !important;
}

.nav-link::before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 0;
  height: 2px;
  background: linear-gradient(90deg, #d35400, #356575) !important;
  transition: all 0.3s ease !important;
  transform: translateX(-50%);
}

.nav-link:hover::before,
.nav-link.active::before {
  width: 80%;
}

.nav-link:hover {
  color: #d35400 !important;
}

.nav-link.active {
  color: #d35400 !important;
  font-weight: 500 !important;
}
